import React, { useEffect, useState } from "react";
import { ApolloProvider } from '@apollo/react-hooks';
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { apolloClient } from "./graphql";
import Home from './pages/Home';
import Main from './pages/Main';
import SignIn from './pages/SignIn';
import SignUp from './pages/SignUp';
import GroupAdd from "./pages/GroupAdd";
import MemberInfo from "./pages/MemberInfo";
import Test from "./pages/test/Test";
import Editor from "./pages/test/Editor";
import Layout from "./pages/test/Layout";
import SliderTest from "./pages/test/SliderTest";
import GroupInfo from "./pages/GroupInfo";
import MyInfo from "./pages/MyInfo";
import Diary from "./pages/Diary";
import GroupJoin from "./pages/GroupJoin";
import DefaultMain from "./pages/DefaultMain";
import DefaultSearch from "./pages/DefaultSearch";

function App() {

  const onUpdateUser = () => {
    console.log("test");
  }

  const SignInObj = () => {
    return (<SignIn onUpdateUser={onUpdateUser}></SignIn>);
  }

  return (
    <ApolloProvider client={apolloClient}>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Routes>

          {/* 로그인전화면 */}
          <Route path="/" element={<DefaultMain />}></Route>
          <Route path="/defaultsearch" element={<DefaultSearch />}></Route>

          {/* 로그인후화면 */}
          <Route path="/home" element={<Home />}></Route>

          {/* 그룹생성 
          1.치료구분 ~ 9.상세정보 */}
          <Route path="/groupadd" element={<GroupAdd />}></Route>

          {/* 그룹상세정보 
          치료사초대 >> 클릭 팝업 */}
          <Route path="/groupinfo" element={<GroupInfo />}></Route>

          {/* 그룹참여신청 
          임시메뉴 >> 그룹참여신청:보호자, 그룹참여신청:치료사, 그룹초대:치료사, 그룹진행여부 */}
          <Route path="/groupjoin" element={<GroupJoin />}></Route>

          {/* 회원상세정보 
          치료사 소개, 치료사 상세, 담당 그룹, 치료후기 */}
          <Route path="/memberinfo" element={<MemberInfo />}></Route>

          {/* 치료일지 
          임시메뉴 >> 치료일지:치료사 , 치료사후기:보호자, 보호자후기:치료사 */}
          <Route path="/diary" element={<Diary />}></Route>

          {/* 나의정보 
          보호자 정보, 어린이 정보, 맞춤정보, 좋아요, 그룹정보, 참여신청
          임시메뉴 >> 나의정보(아바타클릭팝업), 치료사정보 */}
          <Route path="/myinfo" element={<MyInfo />}></Route>

          {/* 로그인 */}
          <Route path="/signin" element={<SignInObj />}></Route>

          {/* 회원가입 */}
          <Route path="/signup" element={<SignUp />}></Route>



          <Route path="/editor" element={<Editor />}></Route>
          <Route path="/main" element={<Main />}></Route>
          <Route path="/test" element={<Test />}></Route>
          <Route path="/layout" element={<Layout />}></Route>
          <Route path="/slidertest" element={<SliderTest />}></Route>
          <Route path="*" element={<div className='error'>에러 페이지</div>} />
        </Routes>
      </BrowserRouter>
    </ApolloProvider>
  );
}

export default App;
